<template>
    <div id="all">
      <otherhead></otherhead>
      <homemain></homemain>
      <otherfoots></otherfoots>
      <otherfoot></otherfoot>
    </div>
</template>
<style>
  *{
    margin:0;
    padding:0;
    list-style:none;
    text-decoration:none;
  }
  #all{
    background:#eee;
  }
  bady{
    background:#f6f6f6;
  }
  #div{
		position: fixed;
    height: 200px;
    width: 1600px;
    top: 0px;
    left: 0px;
			}
			#diva{
				display: inline-block;
				height: 200px;
				animation:width 4s 1 ;
				-webkit-animation:width 4s 1;
				animation-fill-mode: forwards;
				}
				#divb{
				display: inline-block;
				height: 200px;
				animation:width 4s 3.5s 1 ;
				-webkit-animation:width 4s 3.5s 1;
				animation-fill-mode: forwards;
				}
				#divc{
				display: inline-block;
				height: 200px;
				animation:width 4s 7s 1;
				-webkit-animation:width 4s 7s 1;
				animation-fill-mode: forwards;
				}

			.img1{
				width: 229px;
				height: 300px;
				animation:jump 0.6s 1 infinite;
				animation-iteration-count:infinite;
				-webkit-animation:jump 0.6s 1;
				-webkit-animation-iteration-count:infinite;
			}
			.img2{
				width: 60px;
				height: 75px;
				margin-right: 15px;
				animation:jump 0.4s 1 infinite;
				animation-iteration-count:infinite;
				-webkit-animation:jump 0.4s 1;
				-webkit-animation-iteration-count:infinite;
			}
			.img3{
				width: 60px;
				height: 75px;
				margin-right: 15px;
				animation:jump 0.4s 0.25s 1 infinite;
				animation-iteration-count:infinite;
				-webkit-animation:jump 0.4s 0.25s 1;
				-webkit-animation-iteration-count:infinite;
			}
			@-webkit-keyframes jump{
				0%{transform: translate(0px,0px);}
				50%{transform: translate(0px,50px);}
				100%{transform: translate(0px,0px);}
			}
			@keyframes jump{
				0%{transform: translate(0px,0px);}
				50%{transform: translate(0px,50px);}
				100%{transform: translate(0px,0px);}
			}
			@-webkit-keyframes width{
				from%{width: 0%;}
				to{width: 100%;}
			}
			@keyframes width{
				from{width: 0%;}
				to{width: 100%;}
			}
</style>
<script>
	import otherhead from './common/nav.vue'
	import otherfoot from './common/foot.vue'
  import  otherfoots  from  './home/foots.vue'
	import homemain from './home/homeMain.vue'

  export default {
    data() {
      return {}
    },
    components: {
      otherhead,
      otherfoot,
      otherfoots,
      homemain,
    }
  }
</script>




